import React from 'react';
import DrawerToggleButton from '../Sidebar/DrawerToggleButton';

import './Toolbar.css';

const toolBar = props => {
    return (
        <div className="toolbar">
            <DrawerToggleButton click={props.clickHandler}/>
            <h1>{props.pageTitle}</h1>
        </div> 
    );
}

export default toolBar;