import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import MediaQuery from 'react-responsive';

import SideBar from './components/Sidebar/Sidebar';
import Backdrop from './components/Backdrop/Backdrop';
import PortfolioPage from './components/Pages/Portfolio/portfolio';
import AboutPage from './components/Pages/About/about';
import './App.css';

class App extends Component {
  state = {
    sideDrawerOpen: true
  };

  drawerToggleClickHandler = () => {
    this.setState((prevState) => {
      return {sideDrawerOpen: !prevState.sideDrawerOpen};
    })
  };

  backdropClickHandler = () => {
    this.setState({sideDrawerOpen: false});
  }

  render() {
    let mainContent = "main-content";
    let backdrop = "";

    // fix from here
    if(this.state.sideDrawerOpen){
      mainContent = "main-content-push-left";
      backdrop = <Backdrop click={this.backdropClickHandler}/>;
    }

    return (
      <BrowserRouter>
        <React.Fragment>
          <SideBar show={this.state.sideDrawerOpen} />
          <MediaQuery maxWidth={1024}>
            {backdrop}
          </MediaQuery>
          <main className={mainContent}>
            <Switch>
              <Route path="/" render={(props) => <AboutPage {...props} drawerClickHandler={this.drawerToggleClickHandler}/>} exact/>
              <Route path="/portfolio" render={(props) => <PortfolioPage {...props} drawerClickHandler={this.drawerToggleClickHandler} />}/>
            </Switch>
          </main>
        </React.Fragment>
      </BrowserRouter>
    );
  }
}

export default App;
