import React from 'react';
import { NavLink } from 'react-router-dom';
import { SocialIcon } from 'react-social-icons';

import './Sidebar.css';

const sideBar = props => {
    let sideBarClasses = "sidebar";

    if(props.show){
        sideBarClasses = "sidebar open";
    }

    return (
        <header className={sideBarClasses}>
            <div className="sidebar_content">
                <img src={require('./profile_logo.jpg')} className="sidebar_logo" alt="Profile"></img>
                <p>Ken Yeow</p>
                <h4>Software Engineer Student</h4>
            </div>
            <div className="sidebar_navigation-items">
                <ul>
                    <NavLink to="/"><li>About Me</li></NavLink>
                    <NavLink to="/portfolio"><li>Portfolio</li></NavLink>
                </ul>
            </div>
            <div className="sidebar_socialmedia_icons">
                <SocialIcon url="https://www.facebook.com/Kenyeow" />
                <SocialIcon url="https://www.instagram.com/wanshung" />
                <SocialIcon url="https://github.com/kenyeow" />
                <SocialIcon url="https://www.linkedin.com/in/yeow-wan-shung-9a08b4153/" />                 
            </div>
        </header>  
    );
}

export default sideBar;