import React from 'react';
import Toolbar from '../../Toolbar/Toolbar';

import FlareProject from './Project/Flare-Project';
import TwinMixProject from './Project/TwinMix-Project';
import FypProject from './Project/FYP-Project';

import './portfolio.css';

const portfolio = props => {
    return (
        <div className="page-content">
            <Toolbar clickHandler={props.drawerClickHandler} pageTitle="My Projects"/>
            <div className="portfolio-main-content">
                <FypProject/>
                <hr></hr>
                <FlareProject/>
                <hr></hr>
                <TwinMixProject/>
            </div>
        </div>
    );
}

export default portfolio;